// Masthead
//
// Super small header above the content for site name and short description.

.masthead {
  padding-top:    3rem;
  padding-bottom: 0rem;
  margin-bottom: 0rem;
}

.masthead-title {
  margin-top: 0;
  margin-bottom: 0;
  color: #313131
  
  a {
    color: #505050;
  }

  small {
    font-size: 75%;
    font-weight: 400;
    color: #c0c0c0;
    letter-spacing: 0;
  }
}
